html{
  scroll-behavior: smooth;
}
*{
  box-sizing: border-box;
  font-family: "PT Sans", Calibri, Tahoma, sans-serif;
}
body{
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
  color: inherit;
}
ul,h1,h2,h3,h4,h5,h6,p{
  padding: 0;
  margin: 0;
}
li{
  list-style: none;
}

:root{
    /* HEADER */
    --header-height_fullscreen:70px;
    --header-height_phonescreen:200px;
    --header-height-fullscreen:200px;
    --skills-top-phonescreen:70px;
  }
.AboutContainer {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(to right, black 50%, gray);
  display: flex;
  align-items: center;
  padding: 0 25% 0 7%;
  color: white;

  /* background: no-repeat
    url(https://images.unsplash.com/photo-1654871020349-2de959a42b12?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80);
  background-size: cover; */
}

.text {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.text h1 {
  position: relative;
  font-size: 90px;
  margin-bottom: 30px;
  background-image: linear-gradient(to right, red, orange, yellow, green, blue, indigo, violet);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.text p {
  font-size: 32px;
  margin-bottom: 30px;
  line-height: 35px;
  
}

@media screen and (max-width:1200px) {
    .AboutContainer{
      height: 80vh;
    }
}

@media screen and (max-width:900px) {
  .AboutContainer{
    height: -webkit-max-content;
    height: max-content;
  }
}


@media screen and (max-width:650px) {
  .AboutContainer{
    height: -webkit-max-content;
    height: max-content;
  }
  .text h1{
    font-size: 24px;
    margin-bottom: 10px;
  }

  .text p{
    font-size: 16px;
    margin: 10px;
    width: 120%;
  }
}

.ContactContainer {
  background-image: url(/Portfolio/static/media/contact.eaa1041d.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 70vh;
  padding: 20px;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  transition: all 1s ease-in-out;
}

.ContactContainer > h1{
  color: white;
}

.emailMe {
  display: flex;
  flex-direction: column;
  width: 500px;
  grid-gap: 15px;
  gap: 15px;
}

.emailMe > input {
  border: none;
  outline: none;
  height: 2rem;
  font-size: 1.5rem;
  border-radius: 5px;
  background-color: rgba(250, 250, 250, 0.8);
  
}

.emailMe > input:focus {
  background-color: white;
}

.emailMe > .message {
  height: 200px;
  border: none;
  outline: none;
  font-size: 1.5rem;
  border-radius: 5px;
  background-color: rgba(250, 250, 250, 0.8);
}

.emailMe > .message:focus {
  background-color: white;
}

.submit {
  height: 35px;
  font-size: 1.5rem;
  width: 50%;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
}
.submit:hover {
  background-color: #da6c60;
  cursor: pointer; 
  box-shadow: 4px 5px 15px 1px #000000;
}

@media only screen and (max-width: 1250px) {
  .ContactContainer {
    background-image: url(/Portfolio/static/media/contact-phone.72226346.jpg);
    align-items: center;
  }
  .social_icon {
    color: black;
  }

}

@media only screen and (max-width: 900px) {
  .ContactContainer {
    align-items: center;
  }
}


@media only screen and (max-width: 700px) {
  .emailMe {
    width: 100%;
  }
}
.FooterContainer {
  width: 100%;
  height: 100px;
  background-image: linear-gradient(to right, #223442 50%, #4f3029);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.social {
  width: -webkit-max-content;
  width: max-content;
}

.social_list {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.social_item {
  position: relative;
  display: flex;
  align-items: center;
}

.social_link {
  display: flex;
  align-items: center;
}

.social_icon {
  font-size: 30px;
  cursor: pointer;
  color: white;
}

.social_name {
  position: absolute;
  bottom: 5px;
  left: 50px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.6);
  font-size: 25px;
  opacity: 0;
  letter-spacing: 0.8px;
  pointer-events: none;
  z-index: 2;
  padding: 4px 5px;
  width: -webkit-max-content;
  width: max-content;
  transition: all 0.5s ease;
}

.social_link:hover .social_name {
  opacity: 1;
}

.FooterContainer > span {
  font-size: 24px;
  color: white;
}

.Header_Container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 70px;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  transition: height 1s;
  z-index: 999;
}

.Profile_Content {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: -webkit-max-content;
  width: max-content;
}

.Profile_Content ul {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  left: 10px;
  width: -webkit-max-content;
  width: max-content;
  margin-left: 20px;
}

.Profile_Img_Container {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  left: 10px;
}

.Profile_Img_Container img {
  border-radius: 50%;
  width: 60px;
  cursor: pointer;
  transition: -webkit-transform 0.4s ease;
  transition: transform 0.4s ease;
  transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.Profile_Img_Container img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.Profile_Name {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  color: rgb(250, 250, 250);
}

.header_collapsible {
  position: absolute;
  width: -webkit-max-content;
  width: max-content;
  color: rgb(250, 250, 250);
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_list {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  color: white;
  font-size: 20px;
}

.header_list li {
  font-family: "Times New Roman", Times, serif;
  position: relative;
  padding: 5px;
  display: inline-block;
}

.header_list li::after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.header_list li:hover {
  cursor: pointer;
}

.header_list li:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

@media only screen and (max-width: 700px) {
  .Header_Container {
    height: 200px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .Profile_Content {
    flex-direction: column;
    text-align: center;
  }

  .Profile_Img_Container .Profile_Name {
    left: 0;
    margin-left: 0;
  }

  .Profile_Content p {
    font-size: 20px;
  }

  .header_collapsible {
    position: relative;
  }
}

.ProjectsContainer {
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  padding: 30px 10px 70px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(200, 200, 200, 0.6);
  transition: all 1s;
}

.ProjectsContainer h1 {
  margin-bottom: 20px;
}

.Projects {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 30px;
  gap: 30px;
}

.ProjectContainer {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
}

.Project_Wrapper {
  width: 550px;
  height: 400px;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 12px;
  padding: 0 5px;
  grid-gap: 10px;
  gap: 10px;
  box-shadow: 1px 8px 17px 1px rgba(0, 0, 0, 0.85);
  transition: box-shadow 0.5s ease-in-out;
}
.Project_Wrapper:hover {
  box-shadow: 16px 21px 17px 1px rgba(0, 0, 0, 0.85);
}
.Project_Wrapper p {
  position: relative;
  font-size: 1.8rem;
}

.Project_Wrapper p:after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}
.Project_Wrapper p:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
}

.Project_Img {
  width: 100%;
  object-fit: cover;
  height: 300px;
  cursor: pointer;
  border-radius: 12px;
}

.Project_Eplain {
  position: absolute;
  top: 0;
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  background-color: white;
  padding: 10px;
  border-radius: 12px;
  opacity: 0;
  z-index: 1;
  line-height: 20px;
  letter-spacing: 0.5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 1s ease;
}
.ProjectContainer:hover .Project_Eplain {
  opacity: 1;
}
.Project_Eplain h3 {
  margin-bottom: 10px;
}

.Project_Eplain p {
  float: left;
}

@media only screen and (max-width: 700px) {
  .Project_Wrapper {
    width: 350px;
    height: 250px;
  }

  .Project_Img {
    height: -webkit-max-content;
    height: max-content;
  }
}

.skills_container {
  margin-top: 70px;
  transition: margin-top 1s;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skills_wrapper {
  width: 90%;
  height: 100%;
  padding: 10px;
}

.skill_separator {
  width: 5px;
  height: 5px;
  margin: 0px 2px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
}

.skills_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.skills_list_item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  padding: 5px;
  transition: all 0.3s ease-in-out;
  font-size: 1.5rem;
}

.skills_list_item:hover {
  border-radius: 12px;
  box-shadow: -5px 0px 11px 2px #000000;
}

.skills_list_item img {
  width: 40px;
}
.textimg {
  width: 60px;
}

.skills_list_item span {
  pointer-events: none;
}

.React_Icon {
  font-size: 30px;
}

@media only screen and (max-width: 700px) {
  .skills_list {
    grid-gap: 0px;
    gap: 0px;
  }

  .skills_container {
    margin-top: 200px;
  }

  .skills_list_item {
    margin-top: 50px;
  }
  .skills_list_item span {
    font-size: 16px;
  }

  .skill_separator {
    display: none;
  }
}

@media only screen and (max-width: 500px) {
  .skills_list {
    padding: 0px;
  }

  .skills_list_item {
    margin: 3px;
  }

  .React_Icon {
    font-size: 10px;
  }
}

